csui.define('webreports/utils/commands/open.webreport',['require',
    'csui/lib/underscore',
    'csui/lib/jquery',
    'csui/lib/marionette',
    'csui/utils/command.error',
    'csui/utils/commandhelper',
    'csui/models/command',
    'webreports/utils/contexts/factories/run.webreport.pre.controller.factory'
], function (require,
             _,
             $,
             Marionette,
             CommandError,
             CommandHelper,
             CommandModel,
             RunWrPreControllerFactory) {

    var OpenWebReportCommand = CommandModel.extend({

        constructor: function OpenWebReportCommand() {
            CommandModel.prototype.constructor.call(this);
        },

        defaults: {
            signature: 'OpenWebReport',
            scope: "single"
        },

        enabled: function (status) {
            var node = CommandHelper.getJustOneNode(status);
            return node && node.get('type') === 30303;
        },

        execute: function (status, options) {
            var showPrompts,
                self = this,
                deferred = $.Deferred();

            if (!(_.has(options, "node"))) {
                options.node = status.nodes.models[0];
            }

            $.when(this.fetchOriginalNode(options)).then(function() {
                self.RunWRController = options.context.getObject(RunWrPreControllerFactory,{
                    // If we're suppressing the prompt view then we don't want to reuse the prompt params
                    // This causes more than one widget that points to the same WebReport to sync parm views.
                    detached: true,
                    permanent: true,
                    attributes: {
                        id: options.node.get("id")
                    },
                    options: {
                        id: options.node.get("id")
                    }
                });

                self.RunWRController.commandStatus = status;

                // Has to be required in separately to avoid circular dependencies and requireJS timeouts.
                // Ensures parameters are in a uniform format:
                require(['webreports/utils/url.webreports'], function (WRUtil) {

                    if (_.has(options, "parameters")) {
                        options.parameters = WRUtil.getWebReportParametersAsData(options.parameters);
                    }

                    // Hand-off to the controller to determine prompt parameters/destination routes:
                    showPrompts = self.RunWRController.checkForPromptParameters(options);

                    showPrompts.done(function () {
                        deferred.resolve();
                    })
                    .fail(function (error) {
                        deferred.reject();
                    });

                });
            });

            return deferred.promise();
        },

        fetchOriginalNode: function(options) {
            // if launched from a shortcut, the WR node model has a minimal subset of the node properties, (e.g. ID, subtype) but not the node name.
            // Need to do a fetch on the node in order to have a fully populated node model
            var deferred = $.Deferred();

            if (options.node.has('name')) {
                deferred.resolve(); // no fetch needed
            }
            else {
                options.node.fetch({
                    success: deferred.resolve,
                    error: deferred.reject
                });
            }

            return deferred.promise();
        }

    });

    return OpenWebReportCommand;

});
